  created: function () {
    let me = this;
    share();
    //TODO 
    me.getAipRecommend();
    deposit_detail.default.getCreateOrder({
      url: 'queryDrawHistory',
      qs: {
        businessType: '01',
        ccy: 'RMB',
        productType: '1',
        beginDate: '20140301',
        endDate: '20290301'
      }
    }, function (res) {
      let lt = res.rateInfoArray;
      let depTermOne = [];
      let depTermTwo = [];
      let empty = [];
      for (var i= 0;i<lt.length;i++) {
        if (lt[i].depTerm == '180D') {
          depTermTwo.push(lt[i]);
        } else if (lt[i].depTerm == '0D') {
          depTermOne.push(lt[i]);
        }
      }
      for(var n=1;n<5;n++){
        me.commRate(depTermOne,n,0);
        me.commRate(depTermTwo,n,1);
      }

    });
    deposit_detail.default.queryDrawHistory({
      url: 'queryDrawHistory',
      qs: {
        businessType: '00',
        ccy: 'RMB',
        productType: '1',
        beginDate: '20140301',
        endDate: '20290301'
      }
    }, function (res) {
      let lt = res.rateInfoArray;
      let depTerm = [];
      for (var i= 0;i<lt.length;i++) {
        if (lt[i].depTerm === '7D') {
          if (depTerm.indexOf(Number(lt[i].intRate)) < 0) depTerm.push(Number(lt[i].intRate));
        } else if (lt[i].depTerm === '3M') {
          if (depTerm.indexOf(Number(lt[i].intRate)) < 0) depTerm.push(Number(lt[i].intRate));
        } else if (lt[i].depTerm === '6M') {
          if (depTerm.indexOf(Number(lt[i].intRate)) < 0) depTerm.push(Number(lt[i].intRate));
        } else if (lt[i].depTerm === '1Y') {
          if (depTerm.indexOf(Number(lt[i].intRate)) < 0) depTerm.push(Number(lt[i].intRate));
        } else if (lt[i].depTerm === '2Y') {
          if (depTerm.indexOf(Number(lt[i].intRate)) < 0) depTerm.push(Number(lt[i].intRate));
        } else if (lt[i].depTerm === '3Y') {
          if (depTerm.indexOf(Number(lt[i].intRate)) < 0) depTerm.push(Number(lt[i].intRate));
        }
      }
      me.polar2.series[0].data= depTerm.sort();

    });
//    this.getCreateOrder();
//    this.queryDrawHistory();
